// Pytania zostay pobrane
export const questionsFetched = (questions) => ({
  type: 'FETCH_QUESTIONS_SUCCESS',
  questions
});

// Zmiana wartoci input w wyszukiwarce pyta
export const searchQuestions = (newState) => ({
  type: 'SEARCH_QUESTIONS',
  newState
});

// Wybrano kategorie pyta
export const changeCategory = (category) => ({
  type: 'CHANGE_CATEGORY',
  category
});


// Wybrano pytanie 
export const selectCurrentQuestion = (qId) => ({
  type: 'SELECT_CURRENT_QUESTION',
  qId
});

// Lista siowni zostaa pobrana
export const gymsFetched = (gymList) => ({
  type : 'GYM_LIST_FETCHED',
  gymList
});

// Zmienona warto input w wyszukiwarce siowni
export const gymSearchChanged = (input) =>({
  type : 'GYM_SEARCH_CHANGED',
  input
});

// Pobrano dane pojedynczej siowni
export const gymDetailsFetched = (gymDetails) => ({
  type: 'GYM_DETAILS_FETCHED',
  gymDetails
});

// Uytkownik si zalogowa 
// Akcja wykorzystywana rwnie przy aktualizacji stanu po ponownym otwarciu przegldarki
export const logedIn = (data) => ({
  type: 'LOGED_IN',
  data
});

// Uytkownik si wylogowa
// Akcja wykorzystywana rwnie przy aktualizacji stanu po ponownym otwarciu przegldarki
export const loggedOut = () =>({
  type: 'LOGGED_OUT'
});


